body {
  box-sizing: border-box;
  background-color: #f0f4f8;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 2rem 1rem;
  font-family: Inter, sans-serif;
  display: flex;
}

.container {
  background-color: #fff;
  border-radius: 1.5rem;
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  max-width: 28rem;
  padding: 2.5rem;
  display: flex;
  box-shadow: 0 10px 25px #00000019;
}

input[type="text"], input[type="password"] {
  border: 2px solid #cbd5e1;
  border-radius: .75rem;
  width: 100%;
  padding: .8rem 1rem;
  font-size: 1rem;
  transition: border-color .2s ease-in-out;
}

input[type="text"]:focus, input[type="password"]:focus {
  border-color: #60a5fa;
  outline: none;
  box-shadow: 0 0 0 3px #60a5fa4c;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #3b82f6;
  border: none;
  border-radius: .75rem;
  padding: .8rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .2s ease-in-out, transform .1s ease-in-out;
  box-shadow: 0 4px 10px #3b82f64c;
}

#get-weather-btn {
  height: 4em;
}

button:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
}

button:active {
  transform: translateY(0);
}

.weather-display {
  text-align: center;
  color: #1e3a8a;
  background-color: #e0f2fe;
  border-radius: 1rem;
  padding: 1.5rem;
}

.weather-display h2 {
  color: #1d4ed8;
  margin-bottom: .5rem;
  font-size: 1.875rem;
  font-weight: 700;
}

.weather-display p {
  margin-bottom: .5rem;
  font-size: 1.125rem;
  line-height: 1.5;
}

.error-message {
  color: #ef4444;
  text-align: center;
  background-color: #fee2e2;
  border-radius: .75rem;
  padding: 1rem;
  font-weight: 600;
}

.loading-spinner {
  border: 4px solid #00000019;
  border-left-color: #3b82f6;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin: 0 auto;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
